<script>
import {OKXTonConnect,
  OKX_TON_CONNECTION_AND_TRANSACTION_EVENT
} from "../../packages/tonsdk/src"
import {
   OKX_CONNECT_ERROR_CODES
} from "../../packages/core/src/index"
import OKXUniversalProvider from "../../packages/universal-provider/src/UniversalProvider"

var TON = new OKXTonConnect({metaData: {
            name:"OKX Connect Demo",
            icon: window.location.href + "favicon.ico"
        }})
window.ton = TON

import QrcodeVue from 'qrcode.vue'
// import {TonProofService} from "@/ton/ton-proof-service";

export default {
  name: 'App',
  components: {
    QrcodeVue
  },
  data: ()=> {
    return {
      testTopic: '',
      testPublicKey: '',
      testProofPayload: '',
      pendingDeepLink: '-',
      logList: [],
      connectReady: false,
      connectStatusStr: '连接钱包',
      disconnectStatusStr: '断开钱包',
      localPubKey: '',
      localSecKey: '',
      testTransaction:'',
      selectedChain:'',
      testTransactionStr:'发送交易',
      walletAddress:'',
      testboc:'',
      ProofPayloadVerify:'',
      showModal: false,
      showVerifyRow: false,
      qrValue: 'https://www.okx.com/download',
      currentStatus:"",
      isChecked:localStorage.getItem("autoRefresh") == "true",
      handleChange:(isChecked) => {
        localStorage.setItem("autoRefresh",`${isChecked}`)
      }
    }
  },
  computed: {
    getWalletAddress:function (){
      return this.walletAddress;
    },


  },
  methods: {
    async connectBtnClicked() {
      console.log(`encryptStoreMsg   connectBtnClicked  ${Date.now()}`);
      if (window.provider) {
        this.testTonSdk();
      } else {
      console.log(`connectBtnClicked provider not finish init`);
      }

    },

    async disconnectAction() {
      try {
        console.log('get disconnect wallet btn clicked');
        // await TON.disconnect();
        var provider = window.provider
        await provider.disconnect();
        this.walletAddress = 'disconnected'
      } catch (error) {
        console.log('Disconnect  error ==>> ',error.message);
        this.currentStatus = error.message;
      }
    },
    async resotreConnectAction() {
      console.log('get restore wallet btn clicked');
      TON.restoreConnection();
    },
    // clearCacheKeyPair: () => {
    //   console.log(`generate local key pair`)
    //   testCM.cleanCache()
    //   // testTonSdk()
    //
    // },

    async selectedChain1(){
      this.selectedChain = 'eip155:1';
    },
    async selectedChain137(){
      this.selectedChain = 'eip155:137';
    },
    async selectedChain81457(){
      this.selectedChain = 'eip155:81457';
    },


    async mockeRequestAccounts(){
      var data =  {"method":"eth_requestAccounts"}
      this.testTransaction = JSON.stringify(data)
    },
    async eth_chainId(){
      var data =  {"method":"eth_chainId"}
      this.testTransaction = JSON.stringify(data)
    },
    async wallet_switchEthereumChain(){
      var data =  {"method":"wallet_switchEthereumChain"}
      this.testTransaction = JSON.stringify(data)
    },

    async wallet_addEthereumChain(){
      var data = {"method":"wallet_addEthereumChain",
        "params":
            {"blockExplorerUrls":["https://explorer.fuse.io"],
              "chainId":"0x7a",
              "chainName":"Fuse",
              "nativeCurrency":{"name":"Fuse","symbol":"FUSE","decimals":18},
              "rpcUrls":["https://rpc.fuse.io"]
            }
      }

      this.testTransaction = JSON.stringify(data)
    },
    async personal_sign(){
      var data = {"method":"personal_sign","params":{"message":"0x48656c6c6f204170704b697421"}}
      this.testTransaction = JSON.stringify(data)
    },
    async eth_signTypedData_v4(){
      var data = {"method":"eth_signTypedData_v4","params":{"typedDataJson":"{\"domain\":{\"name\":\"Ether Mail\",\"version\":\"1\",\"chainId\":1,\"verifyingContract\":\"0xcccccccccccccccccccccccccccccccccccccccc\"},\"message\":{\"from\":{\"name\":\"Cow\",\"wallet\":\"0xCD2a3d9F938E13CD947Ec05AbC7FE734Df8DD826\"},\"to\":{\"name\":\"Bob\",\"wallet\":\"0xbBbBBBBbbBBBbbbBbbBbbbbBBbBbbbbBbBbbBBbB\"},\"contents\":\"Hello, Bob!\"},\"primaryType\":\"Mail\",\"types\":{\"EIP712Domain\":[{\"name\":\"name\",\"type\":\"string\"},{\"name\":\"version\",\"type\":\"string\"},{\"name\":\"chainId\",\"type\":\"uint256\"},{\"name\":\"verifyingContract\",\"type\":\"address\"}],\"Person\":[{\"name\":\"name\",\"type\":\"string\"},{\"name\":\"wallet\",\"type\":\"address\"}],\"Mail\":[{\"name\":\"from\",\"type\":\"Person\"},{\"name\":\"to\",\"type\":\"Person\"},{\"name\":\"contents\",\"type\":\"string\"}]}}"}}
      this.testTransaction = JSON.stringify(data)
    },
    async eth_sendTransaction(){
      var data = {
        "method": "eth_sendTransaction",
        "params": {
          "gasPrice": "0x17003f6530",
          "nonce": "0x0d30",
          "data": "0x",
          "value": "0x00",
          "to": "0xf2f3e73be57031114dd1f4e75c1dd87658be7f0e",
          "gasLimit": "0x5208",
          "from": "0xf2f3e73be57031114dd1f4e75c1dd87658be7f0e"
        }
      }

      var accounts = await window.provider.request({"method":"eth_requestAccounts"},"eip155");
      var address = accounts[0];
      console.log('当前钱包地址------',address)
      data.params['to'] = address;
      data.params['from'] = address;

      this.testTransaction = JSON.stringify(data)
    },

    async eth_sendTransaction_approve(){
      var data = {
        "method": "eth_sendTransaction",
        "params": {
          "from": "0xf2F3e73be57031114dd1f4E75c1DD87658be7F0E",
          "data": "0x095ea7b3000000000000000000000000f2614a233c7c3e7f08b1f887ba133a13f1eb2c5500000000000000000000000000000000000000000000000000f6f8096ac575b0",
          "to": "0x0b3F868E0BE5597D5DB7fEB59E1CADBb0fdDa50a",
        }
      }
      var accounts = await window.provider.request({"method":"eth_requestAccounts"},"eip155");
      var address = accounts[0];
      console.log('当前钱包地址------',address)
      data.params['from'] = address;
      this.testTransaction = JSON.stringify(data)
    },

    async eth_sendTransaction_mint(){
      var data = {
        "method": "eth_sendTransaction",
        "params": {
          "gas": "0x30795",
          "data": "",
          "to": "0x864baa13e01d8f9e26549dc91b458cd15e34eb7c",
          "value": "0x0",
          "from": "0xf2f3e73be57031114dd1f4e75c1dd87658be7f0e"
        }
      }
     let str = JSON.stringify(data);
      console.log("-----长度--->",str.length);
      var accounts = await window.provider.request({"method":"eth_requestAccounts"},"eip155");
      var address = accounts[0];
      console.log('当前钱包地址------',address)
      data.params['from'] = address;
      this.testTransaction = JSON.stringify(data)
    },


    async wallet_testRpc() {
      let provider = window.provider
      provider.setDefaultChain(`eip155:56`, `https://eth-mainnet.g.alchemy.com/v2/NxUfYJUr-TQtEpLmSXhhD5pXn70GwsyV`);
      var data = {"method":"eth_getBalance","params":["0x8D97689C9818892B700e27F316cc3E41e17fBeb9", "latest"]}
      this.testTransaction = JSON.stringify(data)
    },
    async wallet_watchAsset() {
      var data = {
        "method": "wallet_watchAsset",
        "params": {
          "type":"ERC20",
          "options":{
            "address":"0x1a49351bdb4be48c0009b661765d01ed58e8c2d8",
            "symbol":"YES",
            "image":"https://assets.coingecko.com/coins/images/33052/thumb/200x200_2024_logo.png?1700474858",
            "decimals":18
          },
          "chainId": "eip155:81457",
        },
      }
      this.testTransaction = JSON.stringify(data)
    },



    async sendTransaction(){
      console.log('sendTransaction',window.provider)
      console.log('sendTransaction内容11------',this.testTransaction)
      if (!window.provider){
        console.error('钱包 未初始化完成');
      }

      if (this.testTransaction && window.provider) {
        // eslint-disable-next-line no-undef
        const sendData = JSON.parse(this.testTransaction)
        try {
          console.log('sendTransaction-----22method>>',sendData.method)

          let chainId = 'eip155:1';
          if (this.selectedChain){
            chainId = this.selectedChain;
            console.log('sendTransaction-----this.selectedChain>>',this.selectedChain)
          }
          console.log(`请求数据发送 sendTransaction ----->> ${JSON.stringify(sendData)}`)
          var result = await window.provider.request(sendData,"eip155",chainId)
          console.log(`请求数据回调 sendTransaction ----->> ${JSON.stringify(result)}`)

          if (typeof result == "string") {
            this.testboc = result
          }else {
            this.testboc = JSON.stringify(result)
          }
          console.log('方法数据回调 --this.testboc--',this.testboc)
        } catch (error) {
          console.error('方法数据回调失败 --Error--',error.message)

          if (error.code === OKX_CONNECT_ERROR_CODES.USER_REJECTS_ERROR) {
            this.testboc = "用户拒绝交易"
          } else {
            this.testboc = "签名错误！"
          }
        }
      } else {
        alert("交易数据为空，请选则或输入交易数据。")
      }
    },

    async testTonSdk(){
        // 1. connect
        //  create sub providers for each namespace/chain

        var provider = window.provider;
        await provider.connect({
          namespaces: {
            eip155: {
              chains: ["eip155:1","eip155:66","eip155:81457"],
              events: ["chainChanged", "accountsChanged"],
              rpcMap: {
                1: "https://rpc.walletconnect.com?chainId=eip155:80001",
                66: "",
                81457: "",
              },
              defaultChain:"66"
            },
            pairingTopic: "<123...topic>", // optional topic to connect to
            skipPairing: false, // optional to skip pairing ( later it can be resumed by invoking .pair())
          },
          optionalNamespaces: {
            eip155: {
              chains: ["eip155:137","eip155:56"],
              rpcMap: {
                137: "https://rpc.walletconnect.com?chainId=eip155:80001&projectId",
                56:"https://eth-mainnet.g.alchemy.com/v2/NxUfYJUr-TQtEpLmSXhhD5pXn70GwsyV",
              }
            },
            pairingTopic: "<123...topic>", // optional topic to connect to
            skipPairing: false, // optional to skip pairing ( later it can be resumed by invoking .pair())
          }
        }).then(() => {
          // 2. set defaul chain
          this.walletAddress = "connected";
        });
    },

    closeModal() {
      this.showModal = false;
    },

    isPC() {
      const userAgentInfo = navigator.userAgent
      console.log('current device ua is ===>',userAgentInfo)

      const Agents = ["Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod"]
      let flag = true
      for (let v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = false
          break
        }
      }
      return flag
    },
    isSafariOrChrome() {
      const userAgentInfo = navigator.userAgent
      const Agents = ["Safari", "Chrome"]
      let flag = false
      for (let v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = true
          break
        }
      }
      return flag
    },
  },
  created() {

    for (const eventName of Object.values(OKX_TON_CONNECTION_AND_TRANSACTION_EVENT)) {
      console.log(eventName);
      window.addEventListener(eventName, (event) => {
        console.log(eventName,event.detail)
        this.currentStatus = eventName
      });
    }
  },

  mounted() {
    this.connectReady = true

    OKXUniversalProvider.init({
        projectId: 'YOUR_PROJECT_ID',
        metadata: {
          name: 'React App',
          description: 'React App for WalletConnect',
          url: 'https://walletconnect.com/',
          icon: 'https://avatars.githubusercontent.com/u/37784886'
        },
        client: undefined // optional instance of @walletconnect/sign-client
      }).then((provider)=>{
        window.provider = provider;
        console.log(`init finished: ${provider}`);
        provider.on('connect', (session) => {
          console.log('get connect session', session)
          this.walletAddress = "connected"
        });
        provider.on('reconnect', (session) => {
          console.log('get reconnect session', session)
          this.walletAddress = "connected"
        });
        provider.on('disconnect', () => {
          console.log('get disconnect')
          this.walletAddress = "disconnected"
        });
      })
    

    TON.onStatusChange((walletInfo) => {
      console.log('APP ---TON.onStatusChange>>----', walletInfo);
          if (walletInfo) {
            this.walletAddress = walletInfo.account.address;
            if (this.testProofPayload) {
              var checkProofData = {
                address: this.walletAddress,
                network: walletInfo.account.chain,
                public_key:walletInfo.account.publicKey,
                proof:{
                  ... walletInfo.connectItems.tonProof.proof,
                  state_init: walletInfo.account.walletStateInit
                }
              }
              console.log(`checkProof => ${JSON.stringify(checkProofData)}`)
              // new TonProofService().checkProof(checkProofData).then((result) => {
              //   this.ProofPayloadVerify = `${result}`;
              //   this.showVerifyRow = true;
              // }).catch((error)=>{
              //   this.ProofPayloadVerify = `error ${error}`;
              //   this.showVerifyRow = true;
              // })
            } else {
              this.ProofPayloadVerify = ": no proof";
            }
            this.showModal = false;

          } else {
            this.walletAddress = "disconnected";
          }
        }, (error) => {
          this.showModal = false;
      if (error.code === OKX_CONNECT_ERROR_CODES.USER_REJECTS_ERROR){
        this.walletAddress = `用户拒绝链接: code(${error.code}) `;
      }else {
        this.walletAddress = `链接报错: code(${error.code})  ${error.message}`;
      }
        }
    )
    if (this.isChecked){
      TON.restoreConnection()
    }
